import React from "react";
import Modal from "react-modal";

const Help = ({ isOpen, onRequestClose }) => {
  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onRequestClose}
      contentLabel="Help Modal"
      style={{
        overlay: {
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center"
        },
        content: {
          width: "fit-content",
          maxWidth: "90%",
          maxHeight: "30%",
          margin: "auto",
          padding: "20px",
          borderRadius: "8px",
          boxShadow: "0 2px 4px rgba(0, 0, 0, 0.2)",
          border: "none",
          backgroundColor: "#ffffff",
          textAlign: "center",
          color: "#333",
          animation: "fade-in 0.5s"
        }
      }}
    >
      <h1 style={{ marginBottom: "20px", color: "#1E3A8A" }}>Help</h1>
      <p style={{ marginBottom: "10px" }}>
        <b>Update Graph:</b> This will fetch the most recent stored data from the database.
      </p>
      <p style={{ marginBottom: "10px" }}>
        <b>Export Graph:</b> This will download a local copy of the graph in PNG format.
      </p>
      <p style={{ marginBottom: "10px" }}>
        <b>Export Data:</b> This will download a local copy of the fetched data in CSV format.
      </p>
      <button
        onClick={onRequestClose}
        style={{
          marginTop: "20px",
          padding: "8px 20px",
          backgroundColor: "#D62828",
          color: "#ffffff",
          border: "none",
          borderRadius: "4px",
          cursor: "pointer",
          transition: "background-color 0.3s",
          boxShadow: "0 2px 4px rgba(0, 0, 0, 0.2)"
        }}
      >
        Close
      </button>
    </Modal>
  );
};

export default Help;